.mobileButton {
  position: fixed;
  top: 20px;
  right: 20px;
  display: none;
  font-size: 40px;
  color: white;
  z-index: 100;
  transition: all 0.3s;
}

.mobileButton:hover {
  cursor: pointer;
  color: #71d1d4;
}

@media (max-width: 700px) {
  .mobileButton {
    display: block;
  }
}
