  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(7deg);
            transform: rotate(7deg);
  }
  100% {
    -webkit-transform: rotate(-14deg);
            transform: rotate(-14deg);
  }
}

